
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
XTextarea,
Toast
},
data () {
return {
confrim:"请选择地址",
toast:false,
/* 自提点数据 */
tpl:{},
orderLogistics:'/orderLogistics/',
/* 是否拥有默认数据 */
haveDefault:false,
/* 订单id */
cartId:0,
/* 订单详情数据 */
infoData:{},
/* 时间对照表 */
timeMap:{1:"日",2:"周",3:"月",4:"季",5:"年"},
/* 运费规则列表 */
tplRules:{},
/* 买家留言 */
option:"",
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/* 返回物流方式 */
returnTpl(){
this.getTplPrice();
if(this.$store.state.tplId==1){
return "快递";
}else if(this.$store.state.tplId==2){
return "上门";
}else if(this.$store.state.tplId==3){
this.tpl=this.$store.state.sinceData;
return "自提";
}else{
return "请选择物流方式";
}
},
/* 返回初始和结束周期 */
rentRange(){
let self=this;
let start=dateFormat(new Date(self.infoData.cart_start_time*1000), 'YYYY-MM-DD');
let end=dateFormat(new Date(self.infoData.cart_end_time*1000), 'YYYY-MM-DD');
return {start,end};
},
/* 返回周期名称 */
/* 返回当前周期文字 */
timeText(){
return this.timeMap[this.infoData.rent_period_type];
},
/* 计算商品押金 */
goodsDespoit(){
if(this.infoData.goods_deposit==0){
return 0;
}
let despoite=this.infoData.goods_deposit-this.goodsAllPrice;
if(despoite<=0){
return 0;
}else{
return despoite;
}
},
/* 计算商品总租金 */
goodsAllPrice(){
return this.infoData.rent_period_now_rent_price*this.infoData.cart_time_number*this.infoData.cart_content_good_amount;
},
/* 运费计算 */
returnTplPrice(){
let num=this.infoData.cart_content_good_amount;
let second=num-1;
if(this.$store.state.tplId==1){
return  this.tplRules.freight_first_price+this.tplRules.freight_continued_price*second;
}else if(this.$store.state.tplId==2){
return this.tplRules.freight_door_cost*num;
}else if(this.$store.state.tplId==3){
this.tpl=this.$store.state.sinceData;
return 0;
}else{
return 0;
}
}
},
methods:{
routerBack(){
this.$router.goBack();
},
/* 提交订单 */
buygoods(){
/* 数值校验 */
if(this.getAddress==""){
this.confrim="请选择地址";
this.toast=true;
return false;
}
if(this.returnTpl=="请选择物流方式"){
this.confrim="请选择物流方式";
this.toast=true;
return false;
}

API.order.orderPay({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
info:[{
addressId:this.$store.state.tplId==3? this.$store.state.addressData.id:"",//地址id 物流或者上门选择该字段
cartId: this.cartId, //购物车id
cart_tpl:this.$store.state.tplId,//物流方式
freight_continued_price: this.tplRules.freight_continued_price,//快递续件运费
freight_door_cost: this.tplRules.freight_door_cost,//上门的运费
freight_first_price:this.tplRules.freight_first_price,//快递的收件运费
goodsAmount: this.infoData.cart_content_good_amount,//数量
order_time_number: this.infoData.cart_time_number,//租赁周期
rentTime: this.infoData.cart_start_time,                    rent_period_id: this.infoData.cart_content_rent_prieod_id,                    returnTime: this.infoData.cart_end_time,                    sinceId: this.$store.state.tplId==3?this.$store.state.sinceData.since_id:""
}]
}).then((res)=>{
if(res.body.code==200){
console.log(res.body);
}
});
},
/* 数据初始化 */
Initialization(){
this.cartId=this.$route.params.id;
API.order.orderConfirm({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
cartId:[this.cartId],
}).then((res)=>{
this.infoData=res.body.data.cart_list[0];
this.orderLogistics="/orderLogistics/"+this.infoData.cart_content_goods_id;
});
this.getDefaultAddress();
},
/* 获取用户默认地址，并且出发vuex更新 */
getDefaultAddress(){
API.person.addressInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
is_default:1
}).then((res)=>{
if(res.body.code==200){
this.haveDefault=true;
res= res.body.data.addressList;
this.$store.dispatch('SetAddress',res);
}else{
this.haveDefault=false;
}
});
},
/* 获取运费模板 */
getTplPrice(){
if(this.$store.state.addressData.detail!=""){
API.order.tplPrice({
tpl:this.$store.state.tplId,
province:this.$store.state.addressData.province,
city:this.$store.state.addressData.city,
region:this.$store.state.addressData.district,
goodsId:this.infoData.cart_content_goods_id
}).then((res)=>{
if(res.body.code==200){
this.tplRules=res.body.data;

}
});
}
}
},mounted(){
/* 获取总数据 */
this.Initialization();
},
activated(){
/* 新订单时触发更新 */
if(this.$store.state.currentOrder!=this.cartId){
this.Initialization();
}
}
}



